import AbstractView from '../framework/view/abstract-view';

function createNewPoint() {
  return `
  <button class="trip-main__event-add-btn  btn  btn--big  btn--yellow" type="button" }>New event</button>
  `;
}

/**
 * @class     "New Event"   
 */
export default class NewPointView extends AbstractView {
  #handleNewPointButton = null;

  constructor({ onNewPointButtonClick }) {
    super();
    this.#handleNewPointButton = onNewPointButtonClick;
    this._restoreHandlers();
  }

  get template() {
    return createNewPoint();
  }

  _restoreHandlers() {
    this.element.addEventListener('click', this.#onButtonClick);
  }

  #onButtonClick = () => {
    this.#handleNewPointButton();
  };
}
