import HeaderPresenter from './header-presenter';
import BodyPresenter from './body-presenter';

export default class BoardPresenter {
  #headerContainer = null;
  #bodyContainer = null;
  #pointsModel = null;
  #filterModel = null;
  #headerPresenter = null;
  #bodyPresenter = null;

  /**
   * @constructor
   * @param {Object} params -   
   * @param {*} params.headerContainer -       "NewEvent"
   * @param {*} params.bodyContainer -     
   * @param {*} params.pointsModel -    
   * @description           ,     ,   .
   */
  constructor({ headerContainer, bodyContainer, pointsModel, filterModel }) {
    this.#headerContainer = headerContainer;
    this.#bodyContainer = bodyContainer;
    this.#pointsModel = pointsModel;
    this.#filterModel = filterModel;
  }

  init() {
    this.#renderHeader();
    this.#renderBody();
  }

  #renderHeader() {
    this.#headerPresenter = new HeaderPresenter({
      headerContainer: this.#headerContainer,
      filterModel: this.#filterModel,
      pointsModel: this.#pointsModel,
      onNewPointButtonClick: this.#handleNewPointButton
    });

    this.#headerPresenter.init();
  }

  #renderBody() {
    this.#bodyPresenter =
      new BodyPresenter({
        headerContainer: this.#headerContainer,
        bodyContainer: this.#bodyContainer,
        pointsModel: this.#pointsModel,
        filterModel: this.#filterModel,
      });

    this.#bodyPresenter.init();
  }

  #handleNewPointButton = () => {
    this.#bodyPresenter.createNewPoint();
  };
}
